<template>
	<view class="content">

		<view class="block__title">报修信息</view>
		<view class="ui-select">
			<view class="ui-selectType">报修项目</view>
			<view style="width: 77%; margin: auto;margin-top: 0;display: inline-block;">
				<xfl-select ref="xfl" :list="repairListName" :clearable="false" :showItemNum="5" :listShow="false" :isCanInput="false"
				 :style_Container="'height: 50px; font-size: 16px;border:none'" :placeholder="'请选择'" :selectHideType="'hideAll'">
				</xfl-select>
			</view>
		</view>
		<view class="ui-select">
			<view class="ui-selectType">报修类型</view>
			<view style="width: 77%; margin: auto;margin-top: 0;display: inline-block;">
				<xfl-select ref="typeRepair" :list="repairTypeName" :clearable="false" :showItemNum="5" :listShow="false"
				 :isCanInput="false" :style_Container="'height: 50px; font-size: 16px;border:none'" :placeholder="'请选择'"
				 :selectHideType="'hideAll'">
				</xfl-select>
			</view>
		</view>

		
		<view class="ui-select">
			<view class="ui-selectType">指派师傅</view>
			<view style="width: 77%; margin: auto;margin-top: 0;display: inline-block;">
				<input  disabled class="ui-input" type="text" :value="name!=''?name:''" placeholder-style="color:#bbb" placeholder="选择指派师傅(选填)" @click="chooseRepair" />
				<!-- 		<xfl-select ref="xfl" :list="repairListName" :clearable="false" :showItemNum="5" :listShow="false" :isCanInput="false"
				 :style_Container="'height: 50px; font-size: 16px;border:none'"  :placeholder="'请选择'" :selectHideType="'hideAll'">
				</xfl-select> -->
			</view>
		</view>
		<view class="ui-parkingGroup">
			<view class="ui-parkingTitle">
				预约时间
			</view>
			<!-- <picker class="ui-parkingInput" mode="time" :value="time" start="00:00" end="23:59" @change="bindTimeChange">
				<view class="uni-input" :class="time==null?'ui-parkingTime':''" placeholder="请选择停车时长">{{time==null?'请选择停车时长':time}}</view>
			</picker> -->
			<hTimePicker class="ui-parkingInput" sTime="0" cTime="23" interval="1" @changeTime="changePresetTime">
				<view slot="pCon" class="changeTime" :style="startTime==null?'color:#bbb':''">
					{{startTime==null?"选择预约时间":startTime}}
				</view>
			</hTimePicker>
		</view>
		<view class="ui-parkingGroup">
			<view class="ui-parkingTitle">
				完成时间
			</view>
			<!-- <picker class="ui-parkingInput" mode="time" :value="time" start="00:00" end="23:59" @change="bindTimeChange">
				<view class="uni-input" :class="time==null?'ui-parkingTime':''" placeholder="请选择停车时长">{{time==null?'请选择停车时长':time}}</view>
			</picker> -->
			<hTimePicker class="ui-parkingInput" sTime="0" cTime="23" interval="1" @changeTime="changeCompleteTime">
				<view slot="pCon" class="changeTime" :style="endTime==null?'color:#bbb':''">
					{{endTime==null?"选择完成时间":endTime}}
				</view>
			</hTimePicker>
		</view>

		<view class="cu-form-group">
			<textarea id="context" :value="context" @input="bindInput" placeholder="请输入报修内容"></textarea>
		</view>

		<view class="cu-bar bg-white margin-top">
			<view class="action">
				图片上传
			</view>
			<view class="action">
				{{imgList.length}}/4
			</view>
		</view>
		<view v-show="isShow" class="progress-box">
			<progress :percent="percent" stroke-width="3" />
		</view>
		<view class="cu-form-group">
			<view class="grid col-4 grid-square flex-sub">
				<view class="bg-img" v-for="(img,index) in imgList" :key="index" bindtap="ViewImage" :data-url="imgList[imageIndex]">
					<image :src='imgList[index]' mode='aspectFill'></image>
					<view class="cu-tag bg-red" style="width: 40rpx;
    height: 40rpx;" @tap="deleteImage(index)" :data-index="imageIndex">
						<image class="ui-picturecancle" style="    width: 80%;
    height: 80%;" src="../../static/img/picturecancle.png"
						 mode="aspectFit"></image>
					</view>
				</view>
				<view class="solids" @tap="ChooseImage" v-if="imgList.length<4">
					<image class="" style="height: 130rpx;margin-top: 20rpx;
    width: 130rpx;" src="../../static/img/pictureupload.png"
					 mode="aspectFit"></image>
				</view>
			</view>
		</view>
		<view class="button_up_blank"></view>
		<view class="flex flex-direction">
			<button class="ui-btn color cu-btn bg-green margin-tb-sm lg" style="width: 80%;" @click="bindOwner()">提交</button>
		</view>
	</view>
</template>

<script>
	import xflSelect from '../../components/xfl-select/xfl-select.vue';
	import hTimePicker from "@/components/h-timePicker/h-timePicker.vue";
	import host from '../../common/config.js'
	export default {
		components: {
			// MySelect,
			xflSelect,
			hTimePicker
		},
		data() {
			return {
				startTime: null,
				endTime: null,
				isShow: false,
				percent: null,
				imgList: [],
				imageIndex: 0,
				index: null,
				complaintIndex: 0,
				roomCloums: [],
				roomIdArr: [],
				roomName: "请选择",
				roomId: '',
				roomShow: false,
				columns: ['报修', '建议', '其他'],
				typeCds: ['809001', '809002'],
				typeName: '请选择',
				typeCd: '',
				typeShow: false,
				photoList: [],
				tel: '',
				context: '',
				complaintName: '',
				userId: '',
				storeId: '',
				photos: [],
				communityId: "123",
				// 
				warrid:'',
				repairid:'',
				maintid:'',
				repairList: null,
				repairTypeList: null,
				repairListName: ["无数据"],
				repairTypeName: ["无数据"]
			};
		},
		/**
		 * 生命周期函数--监听页面加载
		 */
		onLoad: function(options) {
			console.log(options)
			let that = this;
			this.$api.getReports().then(res => {
				uni.hideLoading()
				that.repairList = res.data
				uni.hideLoading();
				that.repairListName = that.chooseRepairListName(res.data)

			});
			this.$api.getWarrs().then(res => {
				uni.hideLoading()
				that.repairTypeList = res.data
				console.log(res.data)
				that.repairTypeName = that.chooseRepairListName(res.data)
			})

		},

		/**
		 * 生命周期函数--监听页面初次渲染完成
		 */
		onReady: function() {

		},

		/**
		 * 生命周期函数--监听页面显示
		 */
		onShow: function() {
			var pages = getCurrentPages();
			var currPage = pages[pages.length - 1]; //当前页面
			let currDate = currPage.data.param;
			this.maintid = currDate
		},

		/**
		 * 生命周期函数--监听页面隐藏
		 */
		onHide: function() {},

		/**
		 * 生命周期函数--监听页面卸载
		 */
		onUnload: function() {},

		/**
		 * 页面相关事件处理函数--监听用户下拉动作
		 */
		onPullDownRefresh: function() {},

		/**
		 * 页面上拉触底事件的处理函数
		 */
		onReachBottom: function() {},

		/**
		 * 用户点击右上角分享
		 */
		onShareAppMessage: function() {},
		methods: {
			// 
			chooseRepair() {
				// let obj = {
				// 	"userid": uni.getStorageSync("clientmessageid"),
				// 	"warrid": this.warrid,
				// 	"repairid": this.repairid,
				// 	"repimgs": null,
				// 	"content": this.context,
				// };
				// this.chooseRepairId(this.repairList, this.$refs.xfl._data.selectText)
				// this.chooseRepairId(this.repairTypeList, this.$refs.typeRepair._data.selectText)
				uni.navigateTo({
					url: `/pages/chooseRepair/chooseRepair?id=${this.maintid}`,
				})
			},
			chooseRepairListName(data, listName) {
				const arrName = [];
				data.map((item, index) => {
					arrName.push(item.repairname || item.warrname)
				})
				console.log(arrName)
				return arrName;
			},
			chooseRepairId(data, current) {
				data.map((item, index) => {
					if (item.repairname) {
						if (item.repairname == current) {
							this.repairid = item.id
						}
					} else {
						if (item.warrname == current) {
							this.warrid = item.id
						}
					}
				})
			},
			ChooseImage: function(e) {
				let that = this;
				uni.chooseImage({
					count: 4, //默认9
					sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
					sourceType: ['album'], //从相册选择
					success: function(res) {
						console.log(JSON.stringify(res.tempFilePaths));
						that.$data.imgList.push(res.tempFilePaths[0]);
						const uper = uni.uploadFile({
							url: host + "/api/upload", //仅为示例，非真实的接口地址
							filePath: res.tempFilePaths[0],
							name: 'file',
							formData: {
								'user': 'test'
							},
							success: (uploadFileRes) => {
								that.isShow = false
								that.photos.push(JSON.parse(uploadFileRes.data).data.fileUrl)
								console.log(that.photos)
							}
						});
						uper.onProgressUpdate(function(res) {
							// 进度条等于 上传到的进度
							that.isShow = true
							that.percent = res.progress
							console.log('上传进度' + res.progress)
							console.log('已经上传的数据长度' + res.totalBytesSent)
							console.log('预期需要上传的数据总长度' + res.totalBytesExpectedToSend)
						})
					}
				});


			},
			deleteImage: function(e) {
				console.log(e);
				let imageArr = this.$data.imgList;
				imageArr.splice(e, 1);
			},
			_changeComplaint: function(e) {
				this.typeName = this.columns[e.detail.value];
				this.typeCd = this.typeCds[e.detail.value];
			},
			_changeRoom: function(e) {
				this.roomName = this.roomCloums[e.detail.value];
				this.roomId = this.roomIdArr[e.detail.value];
			},
			changePresetTime(time, timeStamp) {
				this.startTime = time
			},
			changeCompleteTime(time, timeStamp) {
				console.log(time)
				console.log(timeStamp)
				this.endTime = time
			},
			bindInput: function(e) {
				console.log('数据监听', e);
				switch (e.target.id) {
					case "complaintName":
						this.$data.complaintName = e.detail.value;
						break;
					case "tel":
						this.$data.tel = e.detail.value;
						break;
					case "context":
						this.$data.context = e.detail.value;
						break;
				}
				console.log(this);
			},
			bindOwner: function(e) {
				console.log(1231)
				this.chooseRepairId(this.repairList, this.$refs.xfl._data.selectText)
				this.chooseRepairId(this.repairTypeList, this.$refs.typeRepair._data.selectText)
				console.log(this.$refs.xfl._data.selectText);
				let obj = {
					"userid": uni.getStorageSync("clientmessageid"),
					"warrid": this.warrid,
					"repairid": this.repairid,
					"repimgs": null,
					"content": this.context,
					"maintid": this.maintid,
					"startTime": this.startTime,
					"endTime": this.endTime
				};
				let _photos = this.photos;
				let photo = []
				_photos.forEach(function(_item) {
					photo.push(_item);
				});
				let msg = "";
				obj.repimgs = photo.join(",")
				if (obj.repairid == ""||obj.repairid== undefined) {
					msg = "请选择报修项目";
				} else if (obj.warrid == "" ||obj.warrid== undefined) {
					msg = "请选择报修类型";
				} else if (obj.startTime == "" ||obj.startTime== undefined) {
					msg = "请选择预约时间";
				}else if (obj.endTime == "" ||obj.endTime== undefined) {
					msg = "请选择完成时间";
				}else if (obj.content ==
					""||obj.content== undefined) {
					msg = "请填写报修内容";
				}

				if (msg != "") {
					wx.showToast({
						title: msg,
						icon: 'none',
						duration: 2000
					});
				} else {
					console.log("提交数据", obj);
					this.$api.saveWorkOrder(obj).then(res => {
						
						if (res.code == 200) {
							uni.redirectTo({
								url: `../myRepair/myRepair?id=${uni.getStorageSync("clientmessageid")}`
							});
						} else {
							wx.showToast({
								title: res.msg,
								icon: 'none',
								duration: 2000
							});
						}
					})

					//  wx.redirectTo({
					// 	url: '/pages/repairDispatchFinish/repairDispatchFinish',
					// })
				}
			},
			onChange: function(e) {
				console.log(res)

			},
			onTypeConfirm: function(e) {
				console.log("onConfirm", e);
				this.typeName = e.detail.value;
				this.typeCd = e.detail.index ? '809001' : '809002';
				this.typeShow = false;
			},
			onTypeCancel: function(e) {
				this.typeShow = false;
			},
			chooseType: function(e) {
				this.typeShow = true;
			},
			onRoomConfirm: function(e) {
				console.log(this.$refs.xfl);
				console.log("onConfirm", e);
			},
			onRoomCancel: function(e) {
				this.roomShow = false;
			},
			chooseRoom: function(e) {
				this.roomShow = true;
			},
			afterRead: function(event) {
				const {
					file
				} = event.detail;

				let _that = this;

				const {
					photoList = []
				} = this;
				photoList.push(file);
				this.photoList = photoList; // 当设置 mutiple 为 true 时, file 为数组格式，否则为对象格式

				factory.base64.urlTobase64(file.path).then(function(_baseInfo) {
					_that.photos.push(_baseInfo);
				});
				console.log("data信息：", this);
			},
			removePhoto: function(e) {
				console.log(e.detail.index);
				let _photoList = [];
				this.photoList.forEach(function(item, index) {
					if (index != e.detail.index) {
						_photoList.push(item);
					}
				});
				let _photos = [];
				this.photos.forEach(function(item, index) {
					if (index != e.detail.index) {
						_photos.push(item);
					}
				});
				this.photos = _photos;
				this.photoList = _photoList;
			}
		}
	};
</script>
<style>
	.ui-parkingGroup {
		display: flex;
		align-items: center;
		background-color: #fff;
		padding-left: 24rpx;
		border-bottom: 1rpx solid #eef1ef;
		margin-bottom: 32rpx;
	}

	.ui-parkingTextareaTitle {
		height: 100rpx;
		line-height: 100rpx;
		padding-left: 34rpx;
	}

	.ui-parkingTextareaGroup {
		display: flex;
		flex-direction: column;
		align-items: flex-start;
		background-color: #fff;
		border-bottom: 1rpx solid #eef1ef;
		padding-bottom: 100rpx;
	}

	.ui-parkingTitle {
		padding-left: 10rpx;
	}

	.ui-parkingTextarea {
		height: 200rpx;
		width: 90%;
		margin: 0 auto;
		border: 2rpx solid #e6e6e6;
		border-radius: 16rpx;
		padding: 18rpx;
		text-indent: 32rpx;
	}

	.ui-parkingHeader {
		color: #8F8F94;
		padding: 36rpx;
	}

	.ui-parkingContent {
		background-color: #fff;
	}

	.ui-parkingTime {
		color: #808080;
	}

	.ui-parkingInput {
		color: #464646;
		width: 74%;
		height: 100rpx;
		line-height: 100rpx;
		padding-left: 40rpx;
	}

	.content {
		padding-bottom: 300rpx;
	}

	.ui-select {
		width: 100%;
		background-color: #fff;
		margin-bottom: 32rpx;
    display: flex;
    align-items: center;

	}

	.ui-selectType {
		padding-left: 32rpx;
		display: inline-block;
	}

	.ui-parkingUpload {
		width: 100%;
	}

	.ui-picturecancle {
		width: 80%;
		height: 80%;
		margin: 0 auto;
		padding: 4rpx;
		width: 80%;
		height: 80%;
	}

	.fontcolorwhite {
		color: #fff;
		width: 20rpx;
		height: 20rpx;
		font-size: 26px;
	}

	.bg-red {
		background-color: #e54d42;
		color: #ffffff;

	}

	.bg-img {
		background-size: cover;
		background-position: center;
		background-repeat: no-repeat;
	}

	.cuIcon-cameraadd:before {
		content: "\e724";
	}

	.grid.col-4.grid-square>uni-view {
		padding-bottom: calc((100% - 33px)/4);
		height: 0;
		width: calc((100% - 33px)/4);
	}

	.grid.grid-square>uni-view {
		margin-right: 11px;
		margin-bottom: 11px;
		border-radius: 3px;
		position: relative;
		overflow: hidden;
	}

	.cu-tag {
		font-size: 24upx;
		vertical-align: middle;
		position: relative;
		display: inline-flex;
		align-items: center;
		justify-content: center;
		box-sizing: border-box;
		padding: 0upx 16upx;
		height: 48upx;
		font-family: Helvetica Neue, Helvetica, sans-serif;
		white-space: nowrap;
	}

	.cu-tag:not([class*="bg"]):not([class*="line"]) {
		background-color: #f1f1f1;
	}

	.grid {
		display: flex;
		flex-wrap: wrap;
	}

	.grid.grid-square {
		overflow: hidden;
		width: 100%;
	}

	.grid.grid-square .cu-tag {
		position: absolute;
		right: 0;
		top: 0;
		border-bottom-left-radius: 3px;
		padding: 3px 6px;
		height: auto;
		background-color: rgba(0, 0, 0, 0.3);
	}

	.grid.grid-square>view>text[class*="cuIcon-"] {
		font-size: 52upx;
		position: absolute;
		color: #8799a3;
		margin: auto;
		top: 0;
		bottom: 0;
		left: 0;
		right: 0;
		display: flex;
		justify-content: center;
		align-items: center;
		flex-direction: column;
	}

	.solids::after {
		border: 4px solid #eee;
		content: " ";
		width: 200%;
		height: 200%;
		position: absolute;
		top: 0;
		left: 0;
		border-radius: inherit;
		-webkit-transform: scale(0.5);
		transform: scale(0.5);
		-webkit-transform-origin: 0 0;
		transform-origin: 0 0;
		pointer-events: none;
		box-sizing: border-box;
	}

	.uni-image>img {
		-webkit-touch-callout: none;
		-webkit-user-select: none;
		-moz-user-select: none;
		display: block;
		position: absolute;
		top: 0;
		left: 0;
		opacity: 0;
	}

	.grid.grid-square>view {
		margin-right: 19upx;
		margin-bottom: 20upx;
		border-radius: 6upx;
		position: relative;
		overflow: hidden;
	}

	.grid.grid-square>view.bg-img image {
		width: 100%;
		height: 100%;
		position: absolute;
		left: 0;
		bottom: 0;
	}

	.grid.col-1.grid-square>view {
		padding-bottom: 100%;
		height: 0;
		margin-right: 0;
	}

	.grid.col-2.grid-square>view {
		padding-bottom: calc((100% - 20upx)/2);
		height: 0;
		width: calc((100% - 20upx)/2);
	}

	.grid.col-3.grid-square>view {
		padding-bottom: calc((100% - 40upx)/3);
		height: 0;
		width: calc((100% - 40upx)/3);
	}

	.grid.col-4.grid-square>view {
		padding-bottom: calc((100% - 60upx)/4);
		height: 0;
		width: calc((100% - 60upx)/4);
	}

	.grid.col-5.grid-square>view {
		padding-bottom: calc((100% - 80upx)/5);
		height: 0;
		width: calc((100% - 80upx)/5);
	}

	.grid.col-2.grid-square>view:nth-child(2n),
	.grid.col-3.grid-square>view:nth-child(3n),
	.grid.col-4.grid-square>view:nth-child(4n),
	.grid.col-5.grid-square>view:nth-child(5n) {
		margin-right: 0;
	}

	.grid.col-1>view {
		width: 100%;
	}

	.grid.col-2>view {
		width: 50%;
	}

	.grid.col-3>view {
		width: 33.33%;
	}

	.grid.col-4>view {
		width: 25%;
	}

	.grid.col-5>view {
		width: 20%;
	}

	.grid.grid-square {
		overflow: hidden;
	}

	.cu-bar .action:last-child {
		margin-right: 32rpx;
	}

	.cu-bar .action:first-child {
		margin-left: 32rpx;
		font-size: 16px;
	}

	.cu-form-group {
		background-color: #ffffff;
		padding: 0.5px 16px;
		display: -webkit-box;
		display: -webkit-flex;
		display: flex;
		-webkit-box-align: center;
		-webkit-align-items: center;
